$whitecolor: #fff;
$grayborder: #d8d8d8;
$titlecolor: #14142b;
$primarycolor: var(--adminify-primary);
$admin_color: #00ba88;
$admin_plg_status_color: #f4b740;
$admin_plg_status_not_install_color: #1d2327;
$admin_content_body: rgba(78, 75, 102, 0.72);
$admin_heading_color: #fb0066;
$admin_heading_h3_color: #14142b;
$admin_heading_h3_span: #0347ff;
$admin_badge_color: #fff;
$admin_footer_hover: #0347ff;
$admin_popup_color: white;
$admin_popup_close: var(--jltmapfy-popup-color);
$admin_countdown_color: rgb(255, 255, 255 / 0.8);
$admin_popup_button: #222;
$admin_wwc_color: #646970;
$admin_fntfamly_one: dashicons;
$admin_fntfamly_two: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu,
    Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

/* Survey Variables */
$survey_modal_text: rgb(14, 1, 1);
$survey_submit: #fff;
$survey_feedback_text: var(--e-a-color-txt);
$survey_modal_bg: #fff;
$survey_modal_submit_bg: #3d5dff;
$survey_modal_overlay_bg: rgba(0, 0, 0, 0.8);
$survey_feedback_bg: transparent;
$fast: #0cce6b;
$slow: var(--adminify-notif-bg-color);
$blackmuted: rgba(78, 75, 102, 0.72);
$whitegray: var(--adminify-input-bg);

$fontsize: 13px;
$weight400: 400;
$weight600: 600;
$weight700: 700;
$lineheight20: 20px;
$lineheight16: 16px;
$radius6p: 6px;

@mixin font14_400 {
    font-size: 14;
    font-weight: $weight400;
}

@mixin font14_600 {
    font-size: $fontsize;
    font-weight: $weight600;
}

@mixin font14_700 {
    font-size: $fontsize;
    font-weight: $weight700;
}

.wp-adminify--admin-columns--editor--container {
    max-width: 950px;
    margin: 0 auto;
}

#wpadminify-admin-columns {
    margin: 60px 0;
}

#wpadminify-admin-columns .admin-column--message,
.wp-adminify--menu--editor--container #adminify-data-saved-message {
    background-color: #46b450;
    border-radius: 3px;
    color: #fff;
    padding: 8px 15px;
}

#wpadminify-admin-columns {
    .admin-column--message {
        display: inline-block;
    }

    .jltac-tabs {
        align-items: center;
        display: flex;
        justify-content: center;
        .jltac-nav-tabs {
            display: flex;
            border-bottom: 0;
        }

        .jltac-nav-link {
            background-color: $whitecolor;
            border: 1px solid $grayborder;
            border-radius: 0;
            box-shadow: none;
            color: $titlecolor;
            display: inline-block;
            @include font14_600;
            line-height: $lineheight20 + $lineheight16;
            padding: 0 1.5rem;
            display: flex;
            align-content: center;
            gap: 8px;

            &.active {
                background-color: $primarycolor;
                border-color: $primarycolor;
                color: $whitecolor;
                > span {
                    svg {
                        path {
                            stroke: $whitecolor;
                        }
                    }
                }
            }

            > span {
                display: flex;
                align-items: center;
                svg {
                    display: flex;
                    width: 16px;
                }
            }
        }

        li {
            &:first-child {
                .jltac-nav-link {
                    border-right: 0;
                    border-top-left-radius: $radius6p;
                    border-bottom-left-radius: $radius6p;
                }
            }

            &:last-child {
                .jltac-nav-link {
                    border-left: 0;
                    border-top-right-radius: $radius6p;
                    border-bottom-right-radius: $radius6p;
                }
            }
        }
    }

    .jltac-tabs + .jltac-tab-content {
        background: var(--adminify-text-color);
        border: 0;
        border-radius: 6px;
        box-shadow: 0 0 54px 0 rgba(20, 20, 42, 0.07);
        padding: 1.875rem;
        position: relative;

        .jltac-loader {
            height: 44px;
            width: 44px;
            top: 50%;
            left: 50%;
            margin: -22px 0 0 -22px;
            position: absolute;
            z-index: 9;

            border-radius: 50%;
            background: conic-gradient(#0000 10%, #0347ff);
            -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 8px), #000 0);
            animation: wp_adminify_loader_keyframe 1s infinite linear;
        }

        @keyframes wp_adminify_loader_keyframe {
            100% {
                transform: rotate(1turn);
            }
        }

        .jltac-columns {
            margin: 0;
            display: flex;
        }

        .jltac-column {
            display: block;
            flex-basis: 0;
            flex-grow: 1;
            flex-shrink: 1;
            padding: 0;
            &.jltac-is-4 {
                flex: none;
                width: 33.3333333333%;
            }
        }

        .jltac-nav-tabs {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            border: 0;
            margin: 0;
            padding-right: 1.875rem;
            width: 100%;

            li {
                width: 100%;
            }

            .jltac-nav-link {
                align-items: center;
                justify-content: flex-start;
                border: 0;
                border-radius: $radius6p;
                color: $titlecolor;
                display: flex;
                @include font14_600;
                line-height: $lineheight20 + $lineheight16;
                padding: 0 1rem;

                &:hover,
                &.active {
                    background-color: $primarycolor;
                    border: 0;
                    color: $whitecolor;
                    > span {
                        svg {
                            path {
                                stroke: $whitecolor;
                            }
                        }
                    }
                }
                > .jltac-taxonomy-post-type {
                    display: inline-block;
                }
            }
        }

        .jltac-tab-content {
            border-width: 0 0 0 1px;
            border-style: solid;
            border-color: $grayborder;
            padding-left: 1.875rem;

            .jltac-accordion {
                margin-bottom: 10px;
                position: relative;
                z-index: 0;
                background: #fff;
                border: 0;
                border-radius: 6px;
                box-shadow: 0 0 54px 0 rgba(20, 20, 42, 0.07);

                &:last-child {
                    margin-bottom: 0;
                }

                .accordion-opener {
                    background-color: transparent;
                    cursor: pointer;
                    height: 56px;
                    width: 35px;
                    top: 0;
                    right: 0;
                    position: absolute;
                    z-index: 1;
                }

                .jltac-accordion-title {
                    color: $titlecolor;
                    @include font14_700;
                    line-height: $lineheight20 + 4px;
                    padding: 1rem;
                    padding-right: 3rem;
                    margin-right: 3rem;
                    position: relative;
                    display: flex;
                    gap: 8px;

                    &.show {
                        .jltac-accordion-toggler i:before {
                            content: "\e648";
                        }
                    }
                }

                .jltac-accordion-type-title {
                    width: 100%;
                    .jltac-accordion-actions {
                        display: inline-block;
                        font-size: $fontsize - 2px;
                        font-weight: $weight400;
                        margin-left: 15px;
                        opacity: 0;

                        .edit-button {
                            color: $fast;
                            margin-right: 5px;
                        }

                        .remove-button {
                            color: #c30052;
                        }
                    }

                    &:hover {
                        .jltac-accordion-actions {
                            opacity: 1;
                        }
                    }

                    .jltac-accordion-type {
                        font-size: $fontsize - 3px;
                        font-weight: $weight400;
                        margin-right: 10px;
                        float: right;
                        color: #fff;
                        background: $blackmuted;
                        padding: 0 7px;
                        border-radius: 3px;
                    }
                }

                .jltac-accordion-helper {
                    color: $titlecolor;
                    font-size: $fontsize - 2px;
                    line-height: $lineheight20 + $lineheight16;
                    top: 0;
                    bottom: 0;
                    right: -35px;
                    position: absolute;
                    display: inline-flex;
                    gap: 0.5rem;
                    align-items: center;

                    button {
                        background-color: transparent;
                        border: 0;
                        box-shadow: none;
                        cursor: pointer;
                        padding: 4px;
                        > svg {
                            width: 11px;
                        }
                        &.jltac-accordion-remove:hover {
                            > svg {
                                path {
                                    stroke: #c30052;
                                }
                            }
                        }
                    }

                    i {
                        display: inline-block;
                        line-height: $lineheight20 + $lineheight16;
                        padding: 0 5px;
                    }

                    .jltac-accordion-clone {
                        color: $fast;
                    }

                    .jltac-accordion-remove {
                        color: $slow;
                    }

                    .jltac-accordion-toggler {
                        color: $primarycolor;
                    }
                }

                .jltac-accordion-body {
                    border-top: 1px solid $whitegray;
                    display: none;
                    padding: 1rem;

                    .jltac-inner-content {
                        border-top: 1px solid $whitegray;
                        padding: 10px 0;

                        input[type=text],
                        input[type=email],
                        input[type=url],
                        textarea{
                            outline: none;
                            border-color: #dedee7;
                        }

                        &:first-child {
                            border-top: 0;
                        }
                    }

                    .jltac-column {
                        label {
                            color: $titlecolor;
                            @include font14_600;
                        }

                        &.jltac-is-4 {
                            padding: 0 1rem;
                            flex: none;
                            width: 33.3333333333%;
                        }

                        [class*="content-"] {
                            padding: 0;
                        }
                    }

                    select,
                    input:not(
                            [type="checkbox"],
                            [type="radio"],
                            .jltac-range-slider-wrapper .jltac-range-value
                        ) {
                        width: 100%;
                        background-color: var(--adminify-text-color);
                        background-position: right 0 top 55%;
                        border: 1px solid #f1f1f3;
                        border-radius: 6px;
                        color: rgba(78, 75, 102, 0.72);
                        min-height: 36px;
                    }

                    .jltac-range-slider-wrapper {
                        color: $blackmuted;
                        display: inline-block;
                        @include font14_400;
                        width: calc(100% - 100px);
                    }

                    .jltac-range-value {
                        color: $blackmuted;
                        float: left;
                        margin-right: 10px;
                        padding: 8px;
                        width: 50px !important;
                        line-height: 1.2;
                    }

                    .range-slider {
                        border-radius: $radius6p - 3px;
                        float: right;
                        height: 10px;
                        margin-top: 10px;
                        position: relative;
                        // width: calc(100% - 75px);

                        .range-slider-fill{
                            background: rgba(78, 75, 102, 0.72);
                        }

                        .ui-slider-handle {
                            background-color: $grayborder;
                            border-radius: $radius6p - 3px;
                            height: 14px;
                            width: 14px;
                            top: -2px;
                            position: absolute;

                            &:focus-visible {
                                outline: 0;
                            }
                        }
                    }

                    .jltac-unit-labels {
                        float: right;
                        margin-top: 10px;

                        label {
                            color: $blackmuted;
                            display: inline-block;
                        }
                    }
                }
            }
        }
    }

    .jltac-columns {
        padding-top: 0.625rem;
    }

    .jltac-add-button-wrapper {
        display: flex;
        justify-content: end;
        padding-top: 1rem;
    }
    .jltac-save-button {
        margin-top: 1.5rem;
        text-align: center;
    }

    .jltac-popup-button {
        margin-top: 1rem;
    }

    .jltac-button {
        color: #fff;
        background-color: var(--adminify-primary);
        border: 1px solid var(--adminify-primary);
        border-radius: 6px;
        font-size: 13px;
        font-weight: 600;
        height: auto;
        line-height: 34px !important;
        padding: 0 16px !important;
        cursor: pointer;
        &:hover {
            background-color: #fff;
            border-color: var(--adminify-primary);
            color: var(--adminify-primary);
        }
    }
}

.admin-column-name .comment-grey-bubble:before {
    content: "\f101";
    font: normal 20px/0.5 "dashicons";
    speak: none;
    display: inline-block;
    padding: 0;
    top: 8px;
    left: 0;
    position: relative;
    vertical-align: top;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-decoration: none !important;
}

// Popup
#wpadminify-admin-columns {
    .jltac--popup-area {
        background-color: rgba(0, 0, 0, 0.75);
        height: 100%;
        left: 0;
        opacity: 0;
        position: fixed;
        top: 0;
        transition: opacity 0.3s ease-in-out;
        width: 100%;
        z-index: -1;
        .jltac--popup-container {
            width: 32.8125rem;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            margin: 0 auto;
            position: relative;
            top: 50%;
            -webkit-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
            .jltac--popup-container_inner {
                margin: 0 auto;
                max-width: 600px;
                padding: 1.875rem;
                position: relative;
                background: var(--adminify-text-color);
                border: 0;
                border-radius: 6px;
                box-shadow: 0 0 54px 0 rgba(20, 20, 42, 0.07);
            }
        }
        .jltac--popup-close {
            color: #c30052;
            position: absolute;
            right: 1rem;
            top: 0.5rem;
            .dashicons {
                font-size: 1.875rem;
            }
        }
    }
}

.jltac--popup-show .jltac--popup-area {
    opacity: 1 !important;
    z-index: 9999 !important;
}
